.side-nav {
    background-color: var(--primaryColor);
    color: var(--primaryTxt);
    width: 15%;
    height: 100%;
    padding: 1rem;
    font-size: 80%;
}

.h4 {
    margin-bottom: 1.3rem;
    color: var(--secondaryTxt);
}

.hr {
    margin-block: 1.2rem;
}

.list-item {
    display: flex;
    align-items: center;
    margin: .6rem 0rem;
}

.list-item>span {
    margin-right: 1rem;
    color: var(--primaryTxt);
}

@media all and (max-width:900px) {
    .side-nav {
        width: 18%;
    }
}

@media all and (max-width:700px) {
    .side-nav {
        display: var(--show);
        width: 24%;
    }
}

@media all and (max-width:550px) {
    .side-nav {
        display: var(--show);
        width: 32%;
    }
}

@media all and (max-width:420px) {
    .side-nav {
        display: var(--show);
        width: 100%;
    }
}

@media all and (max-width:380px) {
    .side-nav {
        display: var(--show);
        width: 100%;
    }
}