.nav-bar {
    width: 100%;
    padding: 1rem;
    background-color: var(--primaryColor);
    color: var(--primaryTxt);
    display: flex;
    align-items: center;
}

.nav-bar:hover {
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.nav-hamburger-menu {
    display: none;
}

.nav-logo {
    color: var(--logoColor);
    font-size: 30px;
    font-family: 'Rubik Maze', cursive;

    flex: 1
}

.nav-search {
    flex: 1;
}

.nav-search>input {
    width: 100%;
    /* margin: 0% 5%; */
    padding: .7rem;
    border-radius: 10px;
    border: none;
}

.nav-option {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
}

.nav-m-search {
    display: none;
    background-color: var(--primaryColor);
    padding: 1rem;
    text-align: center;
}

.nav-m-search>input {
    width: 80%;
    padding: .7rem;
    border-radius: 10px;
    border: none;
}

@media all and (max-width:700px) {
    .nav-hamburger-menu {
        display: block;
        padding: 0rem .5rem;
    }
}

@media all and (max-width:550px) {
    .nav-bar {
        padding: 0.7rem;
    }

    .nav-logo {
        display: flex;
        justify-content: center;
    }

    .nav-search {
        display: none;
    }

    .nav-option {
        flex: 0;
    }

    .nav-m-search {
        display: block;
        padding: 0.7rem;
    }
}